import React from 'react'
import styled from 'styled-components'
import {
  Card,
  CardBody,
  Flex,
  LaurelLeftIcon,
  LaurelRightIcon,
  Button,
  CheckmarkCircleIcon,
  useWalletModal,
  useModal,
  Heading,Text,Link,OpenNewIcon
} from '@pancakeswap/uikit'
import useAuth from 'hooks/useAuth'
import { useTranslation } from 'contexts/Localization'
import { FINISHED, OVER } from 'config/constants/trading-competition/easterPhases'
import ClaimModal from '../ClaimModal'
import { Heading2Text } from '../CompetitionHeadingText'
import { CompetitionProps } from '../../types'

const StyledCard = styled(Card)`
  display: inline-flex;
  background: linear-gradient(180deg, #7645d9 0%, #452a7a 100%);

  svg {
    margin-bottom: 6px;
    height: 32px;
    width: auto;
    fill: ${({ theme }) => theme.colors.warning};
  }
`
const StyledFlex = styled(Flex)`
  margin-right:10px;
  margin-left:10px;
  ul{
    list-style:none;
  }
`
const StyledButton = styled(Button)`
  margin: 16px 20px 0;
  z-index: 200;

  svg {
    margin: 0 4px 0 0;
    height: 20px;
    width: auto;
    fill: ${({ theme }) => theme.colors.textDisabled};
  }
`

const StyledHeadingText = styled(Heading2Text)`
  white-space: normal;
`

const Footer: React.FC<CompetitionProps> = ({
  userTradingInformation,
  currentPhase,
  account,
  isCompetitionLive,
  profile,
  userCanClaimPrizes,
  finishedAndPrizesClaimed,
  finishedAndNothingToClaim,
  isLoading,
  hasCompetitionEnded,
  onRegisterSuccess,
  onClaimSuccess,
}) => {
  const { t } = useTranslation()
  const { login, logout } = useAuth()
  const { onPresentConnectModal } = useWalletModal(login, logout)

  const [onPresentClaimModal] = useModal(
    <ClaimModal userTradingInformation={userTradingInformation} onClaimSuccess={onClaimSuccess} />,
    false,
  )
  const { hasRegistered, hasUserClaimed } = userTradingInformation
  const registeredAndNotStarted = hasRegistered && !isCompetitionLive && !hasCompetitionEnded

  const isButtonDisabled = Boolean(
    isLoading ||
      currentPhase.state === OVER ||
      registeredAndNotStarted ||
      finishedAndPrizesClaimed ||
      finishedAndNothingToClaim,
  )

  const getHeadingText = () => {
    // Competition live
    if (isCompetitionLive) {
      return t('Now Live!')
    }
    // Competition finished. Rewards being calculated
    if (currentPhase.state === FINISHED) {
      return `${t('Calculating prizes')}...`
    }
    // All competition finished states
    if (hasCompetitionEnded) {
      return `${t('Finished')}!`
    }
    // Competition not started
    return t('Starting Soon')
  }

  const getButtonText = () => {
    // No wallet connected
    if (!account) {
      return t('Unlock Wallet')
    }
    // User not registered
    if (!hasRegistered) {
      return t('I want to Battle!')
    }
    // User registered and competition live
    if (isCompetitionLive) {
      return t('Trade Now')
    }

    // User registered and competition finished
    if (hasCompetitionEnded) {
      // Claim period has ended
      if (currentPhase.state === OVER) {
        return t('Claim period over')
      }
      // User has prizes to claim
      if (userCanClaimPrizes) {
        return t('Claim prizes')
      }
      // User has already claimed prizes
      if (hasUserClaimed) {
        return (
          <>
            <CheckmarkCircleIcon /> {t('Prizes Claimed!')}
          </>
        )
      }
      // User has nothing to claim
      return t('Nothing to claim')
    }

    // User registered but competition has not started
    if (!isCompetitionLive) {
      return (
        <>
          <CheckmarkCircleIcon /> {t('Registered!')}
        </>
      )
    }

    // May be useful for debugging - if somehow none of the above conditions are met
    return 'Whoopsie'
  }

  const handleCtaClick = () => {
    // All conditions when button isn't disabled

    // No wallet connected
    if (!account) {
      onPresentConnectModal()
    }
    // Registered and competition is live
    if (hasRegistered && isCompetitionLive) {
      window.location.href = 'https://exchange.pancakeswap.finance/#/swap'
    }
    // Registered and competition has finished
    if (hasRegistered && hasCompetitionEnded) {
      onPresentClaimModal()
    }
  }

  return (
    <StyledCard>
      <CardBody>
        <Flex flexDirection="column" justifyContent="center" alignItems="center">
          <StyledHeadingText>{t(' ')}</StyledHeadingText>
          {/* Hide button if in the pre-claim, FINISHED phase */}
          <Flex alignItems="flex-start">
              <LaurelLeftIcon />
              {/* <StyledButton disabled={false} onClick={() => handleCtaClick()}>
                {getButtonText()}
              </StyledButton> */}
              <StyledFlex alignItems="flex-left">
                <ul>
                  <li>
                  <Heading as="h4" color="white" mb="16px">
                  {t('Buy')}
                  </Heading>
                  </li>
                  <li>
                  <Link external href="https://">
                    {t("pancakeswap")}
                  </Link>
                  </li>
                </ul>
              </StyledFlex>
              <StyledFlex alignItems="flex-left">
                <ul>
                  <li>
                    <Heading as="h4" color="white" mb="16px">
                    {t('Statistics')}
                  </Heading>
                  </li>
                  <li><Link external href="https://">{t("CoinGecko")}</Link></li>
                  <li><Link external href="https://">{t("Coinmarketcap")}</Link></li>
                </ul>
              </StyledFlex>
              <StyledFlex alignItems="flex-left">
                
                <ul>
                  <li>
                  <Heading as="h4" color="white" mb="16px">
                    {t('Wallet')}
                  </Heading>
                  </li>
                  <li><Link external href="https://">{t("Safepal")}</Link></li>
                  <li><Link external href="https://">{t("MathWallet")}</Link></li>
                  <li><Link external href="https://">{t("TrustWallet")}</Link></li>
                </ul>
              </StyledFlex>
              <StyledFlex alignItems="flex-left">
                
                <ul>
                  <li>
                  <Heading as="h4" color="white" mb="16px">
                    {t('Social')}
                  </Heading>
                  </li>
                  <li><Link external href="https://">{t("Telegram")}</Link></li>
                  <li><Link external href="https://">{t("Github")}</Link></li>
                  <li><Link external href="https://">{t("Medium")}</Link></li>
                  <li><Link external href="https://">{t("Twitter")}</Link></li>
                </ul>
              </StyledFlex>
              <LaurelRightIcon />
          </Flex>
        </Flex>
      </CardBody>
    </StyledCard>
  )
}

export default Footer
